.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }

  .mobile {
    background: linear-gradient(to bottom right, #8a0000, #1a1a8e);
    width: 255px;
    height: 530px;
    border-radius: 42px;
  }
  
  
  .passcode-input {
    font-size: 18px;
    text-align: center;
    padding: 6px 6px 7px 6px;
    width: 150px;
    margin: 82px 0px 20px 53px;
    background-color: rgb(260 260 260 / 0.3);
    color: #fff;
    border-radius: 53px;
}
  
  .keyboard {
    place-items: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
  }
  
  .keyboard button {
    font-size: 18px;
    padding: 9px;
    width: 60px;
    height: 60px;
    border: 0.1px solid #fff;
    border-radius: 50%;
    background-color: rgb(260 260 260 / 0.3);
    cursor: pointer;
    color: #fff;
}

  .keyboard button:last-child {
    grid-column: 2;
  }
  
  .keyboard button:hover {
    background-color: #e0e0e0;
  }
  
  .submit-btn {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
  }
  
  .submit-btn:hover {
    background-color: #0056b3;
  }
  

  .zero {
    position: relative;
    left: 89.1px;
  }